.PaperButtons {
  position: absolute;
  z-index: 50;
  width: 100%;
  top: 0;
  left: 50%;
  padding: 15px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  background: rgba(0,0,0,0.8);
}



